import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path:'/',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      children:[
        {
          path:'/index',
          name:'index',
          component:resolve => require(['../components/index/Index.vue'], resolve),
        },
        {
          path:'/equipmentList',
          name:'equipmentList',
          component:resolve => require(['../components/equipment/equipmentList.vue'], resolve),
        },
        {
          path:'/equipmentCharts',
          name:'equipmentCharts',
          component:resolve => require(['../components/equipment/equipmentCharts.vue'], resolve),
        },
        {
          path:'/allEquipmentCharts',
          name:'allEquipmentCharts',
          component:resolve => require(['../components/equipment/allEquipmentCharts.vue'], resolve),
        },
        {
          path:'/flowIndex',
          name:'flowIndex',
          component:resolve => require(['../components/flowManage/flowIndex.vue'], resolve),
        },
        {
          path: '/userAgentInfo',
          name:'userAgentInfo',
          component: resolve => require(['../components/flowManage/userAgentInfo.vue'], resolve),
          meta: { title: 'User-Agent信息' }
        },
        {
          path: '/userAgentInfo2',
          name:'userAgentInfo2',
          component: resolve => require(['../components/flowManage/userAgentInfo2.vue'], resolve),
          meta: { title: 'User-Agent信息' }
        },
        {
          path: '/realTimeFlow',
          name:'realTimeFlow',
          component: resolve => require(['../components/flowManage/realTimeFlow.vue'], resolve),
          meta: { title: '全局实时流量' }
        },
        {
          path: '/IPHostFlow',
          name:'IPHostFlow',
          component: resolve => require(['../components/flowManage/IPHostFlow.vue'], resolve),
          meta: { title: 'IP主机流量' }
        },
        {
          path: '/IPHostFlow2',
          name:'IPHostFlow2',
          component: resolve => require(['../components/flowManage/IPHostFlow2.vue'], resolve),
          meta: { title: 'IP主机流量' }
        },
        {
          path: '/protocolFlow',
          name:'protocolFlow',
          component: resolve => require(['../components/flowManage/protocolFlow.vue'], resolve),
          meta: { title: '协议流量' }
        },
        {
          path: '/protocolFlow2',
          name:'protocolFlow2',
          component: resolve => require(['../components/flowManage/protocolFlow2.vue'], resolve),
          meta: { title: '协议流量' }
        },
        {
          path: '/mulAndBro',
          name:'mulAndBro',
          component: resolve => require(['../components/flowManage/mulAndBro.vue'], resolve),
          meta: { title: '广播包/组播包' }
        },
        {
          path: '/mulAndBro2',
          name:'mulAndBro2',
          component: resolve => require(['../components/flowManage/mulAndBro2.vue'], resolve),
          meta: { title: '广播包/组播包' }
        },
        {
          path: '/packetType',
          name:'packetType',
          component: resolve => require(['../components/flowManage/packetType.vue'], resolve),
          meta: { title: '数据包类型' }
        },
        {
          path: '/packetType2',
          name:'packetType2',
          component: resolve => require(['../components/flowManage/packetType2.vue'], resolve),
          meta: { title: '数据包类型' }
        },
        {
          path: '/equipmentFlow',
          name:'equipmentFlow',
          component: resolve => require(['../components/flowManage/equipmentFlow.vue'], resolve),
          meta: { title: '资产流量' }
        },
        {
          path: '/equipmentFlow2',
          name:'equipmentFlow2',
          component: resolve => require(['../components/flowManage/equipmentFlow2.vue'], resolve),
          meta: { title: '资产流量' }
        },
        {
          path: '/portFlow',
          name:'portFlow',
          component: resolve => require(['../components/flowManage/portFlow.vue'], resolve),
          meta: { title: '端口流量' }
        },
        {
          path: '/portFlow2',
          name:'portFlow2',
          component: resolve => require(['../components/flowManage/portFlow2.vue'], resolve),
          meta: { title: '端口流量' }
        },
        {
          path: '/performanceAnalysis',
          name:'performanceAnalysis',
          component: resolve => require(['../components/flowManage/performanceAnalysis.vue'], resolve),
          meta: { title: '应用性能分析' }
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['../components/login/login.vue'], resolve)
    }
  ]
})



