// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {Message,MessageBox,Loading } from 'element-ui'
import axios from  'axios'
import {get,post} from './axios/axios'
import echarts from 'echarts';
import vueHashCalendar from 'vue-hash-calendar'
// 引入组件CSS样式
import 'vue-hash-calendar/lib/vue-hash-calendar.css'
// 注册组件库
Vue.use(vueHashCalendar)
Vue.prototype.$echarts = echarts;

Vue.prototype.$Message = Message;
Vue.prototype.$MessageBox = MessageBox;
Vue.prototype.$get = get;
Vue.prototype.$post = post;

Vue.config.productionTip = false;

//使用钩子函数对路由进行权限跳转
router.beforeEach((to,from,next)=>{
  let loadingInstance = Loading.service({ fullscreen: true });
  //console.log(to.path)
  if (to.path !== '/login'){
    get('/user/checkLogin.do',{})
      .then((res)=>{
        loadingInstance.close();
        if (res.success === 'false'){//登录过期
          Message.error('登录已过期,请重新登录');
          setTimeout(()=>{next('/login');},2000)
        }else{
          //获取本地存储的路由
          let newRouterVal = JSON.parse(sessionStorage.getItem(to.path));
          //删除本地存储的路由，防止进入死循环
          sessionStorage.removeItem(to.path);
          //console.log(to.path)
          //判断是否是刷新页面
          if(newRouterVal !== null){//是
            let newRouters = [{
              path:'/',
              component: resolve => require(['@/components/common/Home.vue'], resolve),
              meta: { title: '自述文件' },
              children:[
                {
                  path:'/'+ newRouterVal.path,
                  name:newRouterVal.path,
                  component: resolve => require(['@/components/'+ newRouterVal.component], resolve),
                  meta: { title: newRouterVal.title }
                }
              ]
            }]
            router.addRoutes(newRouters);
            next({...to});


          }else {
            next()
          }
          //next();
        }
      })
  }else{
    loadingInstance.close();
    next()
  }
})

Vue.use(ElementUI,{ size: 'small' });
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



